.slick-dots {
	align-items: center;
	bottom: -2rem;
	display: flex;
	justify-content: center;
	height: fit-content;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	& > * {
		&:not(:last-child) { margin-right: 10px; }
	}
	button {
		border-radius: 5px;
		border: 0;
		background-color: rgba($base-text, .4);
		cursor: pointer;
		display: block;
		height: 10px;
		padding: 0;
		text-indent: -9999px;
		transition: all linear .3s;
		width: 30px;
	}
	.slick-active {
		button {
			background-color: $action-color;
		}
	}
}